.breed-select {
  font-size: 22px;
  display: none;
}

.label {
  font-size: 30px;
}

.loader {
  perspective: 1000px;
  width: 48px;
  height: 48px;
  color: #100f0f;
  border-radius: 50%;
  display: block;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader:after {
  color: #ff3d00;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .2em 0;
  }

  12% {
    box-shadow: .2em .2em;
  }

  25% {
    box-shadow: 0 .2em;
  }

  37% {
    box-shadow: -.2em .2em;
  }

  50% {
    box-shadow: -.2em 0;
  }

  62% {
    box-shadow: -.2em -.2em;
  }

  75% {
    box-shadow: 0 -.2em;
  }

  87% {
    box-shadow: .2em -.2em;
  }
}

.error {
  display: none;
}

.cat-info {
  flex-wrap: wrap;
  font-size: 22px;
  display: flex;
}

.cat-info img {
  max-width: 600px;
  margin-top: 20px;
  margin-right: 20px;
}

.cat-description {
  max-width: 600px;
}

